@font-face {
  font-family: 'lam-font';
  src: url('../font/lam-font.eot?592460');
  src: url('../font/lam-font.eot?592460#iefix') format('embedded-opentype'),
       url('../font/lam-font.woff2?592460') format('woff2'),
       url('../font/lam-font.woff?592460') format('woff'),
       url('../font/lam-font.ttf?592460') format('truetype'),
       url('../font/lam-font.svg?592460#lam-font') format('svg');
  font-weight: normal;
  font-style: normal;
}
