@use "../../../assets/styles/utils" as *;
@use "sass:color";

.Accordion {
  border: solid 1px $c-row-border;
  background-color: $c-bright;
  background-clip: padding-box;
  font-size: 14px;

  & + & {
    border-top-width: 0px;
  }

  &__header {
    transition: color 0.3s;
    height: 49px;
    padding: 14px 69px 11px 24px;
    position: relative;

    &:hover {
      cursor: pointer;
    }

    .DropdownIcon {
      width: 10px;
      height: 6px;
      display: block;
      position: absolute;
      right: 24px;
      top: 23px;
      stroke: $c-primary;
      fill: $c-primary;
      transition: transform 0.3s, stroke 0.3s, fill 0.3s;
    }

    &--expanded {
      color: $c-secondary;
      .DropdownIcon {
        transform: rotate(-180deg);
        stroke: $c-secondary;
        fill: $c-secondary;
      }
    }
  }

  &__content {
    max-height: 0px;
    transition: max-height 0.5s cubic-bezier(0, 1, 0, 1);
    overflow: hidden;

    &--expanded {
      transition: max-height 0.5s ease-in-out;
      max-height: 900px;
    }

    &__wrapper {
      padding: 13px 40px 40px 24px;
      display: flex;

      &__text {
        flex: 1;

        ul,
        ol {
          padding-left: 18px;
        }

        li {
          margin-top: 8px;
        }

        p + p {
          margin-top: 20px;
        }

        > ol {
          list-style-type: decimal;

          > li + li {
            margin-top: 24px;
          }
        }

        ul {
          list-style-type: disc;

          > li {
            > ul {
              list-style-type: none;

              > li {
                text-indent: -9px;

                &:before {
                  content: "– ";
                  text-indent: -9px;
                }

                > ul {
                  list-style-type: none;

                  > li {
                    text-indent: -9px;

                    &:before {
                      content: "* ";
                      text-indent: -9px;
                    }
                  }
                }
              }
            }
          }
        }
      }

      &__media {
        width: 229px;
        height: 144px;
        border-radius: 6px;
        background-size: cover;
        background-position: center;
        margin: 0 24px 0 32px;

        &:hover {
          cursor: pointer;

          .PlayIcon {
            filter: drop-shadow(0px 0px 10px black);
          }
        }

        &--video {
          box-shadow: inset 0 0 0 2000px color.adjust($c-primary, $alpha: -0.5);
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }

  &:first-child {
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
  }

  &:last-child {
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
  }
}
