@use "global";

$font-url: "../../fonts";

@font-face {
  font-family: "Telefonica";
  font-style: normal;
  font-weight: global.$fw-regular;
  src: url("#{$font-url}/Telefonica-Regular.woff") format("woff"),
    url("#{$font-url}/Telefonica-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Telefonica";
  font-style: normal;
  font-weight: global.$fw-light;
  src: url("#{$font-url}/Telefonica-Light.woff") format("woff"),
    url("#{$font-url}/Telefonica-Light.ttf") format("truetype");
}

@font-face {
  font-family: "Telefonica";
  font-style: normal;
  font-weight: global.$fw-bold;
  src: url("#{$font-url}/Telefonica-Bold.woff") format("woff"),
    url("#{$font-url}/Telefonica-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "icomoon";
  src: url("#{$font-url}/icomoon.eot?45fsgp");
  src: url("#{$font-url}/icomoon.eot?45fsgp#iefix") format("embedded-opentype"),
    url("#{$font-url}/icomoon.ttf?45fsgp") format("truetype"),
    url("#{$font-url}/icomoon.woff?45fsgp") format("woff"),
    url("#{$font-url}/icomoon.svg?45fsgp#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

@font-face {
  font-family: "Roboto";
  src: url("#{$font-url}/Roboto-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: global.$fw-regular;
}

@font-face {
  font-family: "Work Sans";
  src: url("#{$font-url}/WorkSans-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: global.$fw-regular;
}

@font-face {
  font-family: "Montserrat";
  src: url("#{$font-url}/Montserrat-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: global.$fw-regular;
}

@font-face {
  font-family: "Noto Sans";
  src: url("#{$font-url}/NotoSans-Regular.ttf") format("opentype");
  font-style: normal;
  font-weight: global.$fw-regular;
}

@font-face {
  font-family: "Noto Serif";
  src: url("#{$font-url}/NotoSerif-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: global.$fw-regular;
}